import Menu from '@components/Menu';
import Modal from '@components/Modal';
import useInput from '@hooks/useInput';

import { Button, Input, Label } from '@pages/member/SignUp/styles';
import gravatar from 'gravatar';
import React, { useCallback, useState } from 'react';
import { Link, Navigate, useNavigate } from 'react-router-dom';
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Outlet } from 'react-router-dom';

import {
  AddButton,
  CenterDiv,
  Header,
  LogOutButton,
  ProfileImg,
  ProfileModal,
  RightDiv,
  RightMenu,
  WorkspaceButton,
  Workspaces,
  WorkspaceWrapper,
} from './styles';
import useCustomMember from '@hooks/useCustomMember';
import Playbar from '@components/Playbar';
import { IWorkspace } from '@typings/db';
import { AxiosError } from 'axios';
import useCustomWorkspace from '@hooks/useCustomWorkspace';
import { Scrollbars } from 'react-custom-scrollbars-2';
import { Resizable } from 're-resizable';
import { removeCookie } from '@utils/cookieUtil';

const BasicLayout = () => {
  const { isLogin, memberState, deleteSlices, moveToPath, updateSlices } = useCustomMember();
  // const { playbarState } = useCustomPlaybar();
  // const { currentTime, selectedTime, selectedPoint } = playbarState;
  const { postCreateWorkspaceAsyncHook } = useCustomWorkspace();
  // const draggableRef = useRef(null);
  const navigate = useNavigate();

  const [newWorkspace, onChangeNewWorkspace, setNewWorkspace] = useInput('');
  // const [newUrl, onChangeNewUrl, setNewUrl] = useInput('');
  const [nameConflict, setNameConflict] = useState(false);
  const [urlConflict, setUrlConflict] = useState(false);

  const [showUserMenu, setShowUserMenu] = useState(false);
  const [showCreateWorkspaceModal, setShowCreateWorkspaceModal] = useState(false);

  const onCreateWorkspace = useCallback(
    async (e: React.FormEvent<HTMLFormElement>) => {
      e.preventDefault();
      const nameRegex = /^[a-zA-Z0-9가-힣\s]+$/; // 한글, 영어, 숫자, 띄어쓰기 허용
      if (!newWorkspace || !nameRegex.test(newWorkspace)) {
        toast.error('Workspace name must not contain special characters.');
        return;
      }

      // if (!newUrl || !newUrl.trim()) return;
      // url 띄어쓰기 대체
      const sanitizedUrl = newWorkspace.trim().replace(/\s+/g, '-');

      try {
        const response = await postCreateWorkspaceAsyncHook({ name: newWorkspace, url: sanitizedUrl });
        await updateSlices(response.url);
        setShowCreateWorkspaceModal(false);
        setNewWorkspace('');
        // setNewUrl('');
        setNameConflict(false);
        setUrlConflict(false);
        toast.dismiss();
        toast.success('Workspace created successfully', {
          closeOnClick: true,
        });
        moveToPath(`/workspace/${response.url}/analyze`);
      } catch (error) {
        const axiosError = error as AxiosError;
        if (axiosError.response?.status === 409) {
          const errorMessage = axiosError.response?.data as string;
          if (errorMessage.includes('name')) {
            setNameConflict(true);
          } else if (errorMessage.includes('url')) {
            setUrlConflict(true);
          }
          toast.error('Workspace name or URL already exists. Please choose a different one.');
        } else {
          toast.error('An unexpected error occurred. Please try again later.');
          console.error('Detailed Error:', error);
        }
      }
    },
    [moveToPath, newWorkspace, postCreateWorkspaceAsyncHook, setNewWorkspace],
  );

  const handleClickLogout = useCallback(async () => {
    try {
      removeCookie('member');
      await deleteSlices();
      navigate({ pathname: '/member/login' });
      // moveToPath('/member/signup');
      toast.dismiss();
      toast.success('Logged out successfully', {
        closeOnClick: true,
      });
      setShowUserMenu((prev) => !prev);
    } catch (error) {
      toast.error('Failed to log out', { position: 'bottom-center' });
    }
  }, [deleteSlices, navigate]);

  const onClickCreateWorkspace = useCallback(() => {
    setShowCreateWorkspaceModal(true);
  }, []);

  const onClickUserProfile = useCallback(() => {
    setShowUserMenu((prev) => !prev);
  }, []);

  const onCloseModal = useCallback(() => {
    setShowCreateWorkspaceModal(false);
  }, []);

  if (!memberState) {
    return <Navigate to="/member/login" />;
  }
  return (
    <div>
      <Header>
        {isLogin && memberState && (
          <RightMenu>
            <span onClick={onClickUserProfile}>
              <ProfileImg src={gravatar.url(memberState.email, { s: '28px', d: 'mp' })} alt={memberState.nickname} />
            </span>
            {showUserMenu && (
              <Menu style={{ right: 0, top: 38 }} show={showUserMenu} onCloseModal={onClickUserProfile}>
                <ProfileModal>
                  <img src={gravatar.url(memberState.email, { s: '36px', d: 'mp' })} alt={memberState.nickname} />
                  <div>
                    <span id="profile-name">{memberState.nickname}</span>
                    <span id="profile-active">Active</span>
                  </div>
                </ProfileModal>
                <LogOutButton onClick={handleClickLogout}>Logout</LogOutButton>
              </Menu>
            )}
          </RightMenu>
        )}
      </Header>
      <WorkspaceWrapper>
        <Resizable
          defaultSize={{ width: 65, height: '100%' }}
          minWidth={65}
          maxWidth={260}
          enable={{
            right: true,
          }}
        >
          <Workspaces style={{ height: 'calc(100vh - 38px)' }}>
            <Scrollbars>
              {isLogin && <AddButton onClick={onClickCreateWorkspace}>+</AddButton>}
              {memberState?.workspaces
                ?.slice() // 원본 배열 복사
                .sort((a, b) => b.id - a.id) // 최신순 정렬
                .map((ws: IWorkspace, index) => (
                  <Link key={`${ws.id}-${index}`} to={`/workspace/${ws.url}/`}>
                    <WorkspaceButton>{ws.name}</WorkspaceButton>
                  </Link>
                ))}
            </Scrollbars>
          </Workspaces>
        </Resizable>
        <CenterDiv>
          {/*<Map />*/}
          <Map2 />
          {/*<Map3 />*/}
          <Playbar />
        </CenterDiv>
        <RightDiv>
          <Outlet />
        </RightDiv>
      </WorkspaceWrapper>
      <Modal show={showCreateWorkspaceModal} onCloseModal={onCloseModal}>
        <form onSubmit={onCreateWorkspace}>
          <Label id="workspace-label">
            <span>Workspace name</span>
            <Input
              id="workspace"
              value={newWorkspace}
              onChange={(e) => {
                onChangeNewWorkspace(e);
                setNameConflict(false);
              }}
              style={{ borderColor: nameConflict ? 'red' : 'initial' }}
            />
          </Label>
          {/*<Label id="workspace-url-label">*/}
          {/*  <span>Workspace url</span>*/}
          {/*  <Input*/}
          {/*    id="workspace-url"*/}
          {/*    value={newUrl}*/}
          {/*    onChange={(e) => {*/}
          {/*      onChangeNewUrl(e);*/}
          {/*      setUrlConflict(false);*/}
          {/*    }}*/}
          {/*    style={{ borderColor: urlConflict ? 'red' : 'initial' }}*/}
          {/*  />*/}
          {/*</Label>*/}
          <Button type="submit">Create</Button>
        </form>
      </Modal>
      <ToastContainer position="bottom-center" />

      {/*<Draggable nodeRef={draggableRef}>*/}
      {/*  <InfoDiv ref={draggableRef}>*/}
      {/*    <ContentRow>*/}
      {/*      <RightContent className={'whiteFont margin0 padding0'}>*/}
      {/*        <ContentLineText className={'whiteFont margin0 padding0'}>*/}
      {/*          [변수값 보기 용] 드레그 시 움직입니다.*/}
      {/*        </ContentLineText>*/}
      {/*        <ContentLineText className={'whiteFont margin0 padding0'}>currentTime : [{currentTime}]</ContentLineText>*/}
      {/*        <ContentLineText className={'whiteFont margin0 padding0'}>*/}
      {/*          selectedTime : [{selectedTime}]*/}
      {/*        </ContentLineText>*/}
      {/*        <ContentLineText className={'whiteFont margin0 padding0'}>*/}
      {/*          selectedTraveledDistance : [{selectedPoint?.traveledDistance}]*/}
      {/*        </ContentLineText>*/}
      {/*        <ContentLineText className={'whiteFont margin0 padding0'}>*/}
      {/*          selectedLatitude : [{selectedPoint?.coordinate?.latitude}]*/}
      {/*        </ContentLineText>*/}
      {/*        <ContentLineText className={'whiteFont margin0 padding0'}>*/}
      {/*          selectedLongitude : [{selectedPoint?.coordinate?.longitude}]*/}
      {/*        </ContentLineText>*/}
      {/*      </RightContent>*/}
      {/*      <RightContent className={'whiteFont margin0 padding0'}>*/}
      {/*        <ContentLineText className={'whiteFont margin0 padding0'}>*/}
      {/*          firstGate : [{playbarState?.firstGate && playbarState.firstGate.sequence}]*/}
      {/*        </ContentLineText>*/}
      {/*        <ContentLineText className={'whiteFont margin0 padding0'}>*/}
      {/*          lastGate : [{playbarState?.lastGate && playbarState.lastGate.sequence}]*/}
      {/*        </ContentLineText>*/}
      {/*        <ContentLineText className={'whiteFont margin0 padding0'}>*/}
      {/*          previousGateBasedOnSelected : [*/}
      {/*          {playbarState?.previousGateBasedOnSelected && playbarState.previousGateBasedOnSelected.sequence}]*/}
      {/*        </ContentLineText>*/}
      {/*        <ContentLineText className={'whiteFont margin0 padding0'}>*/}
      {/*          latestGateBasedOnSelected : [*/}
      {/*          {playbarState?.latestGateBasedOnSelected && playbarState.latestGateBasedOnSelected.sequence}]*/}
      {/*        </ContentLineText>*/}
      {/*        <ContentLineText className={'whiteFont margin0 padding0'}>*/}
      {/*          nextGateBasedOnSelected : [*/}
      {/*          {playbarState?.nextGateBasedOnSelected && playbarState.nextGateBasedOnSelected.sequence}]*/}
      {/*        </ContentLineText>*/}
      {/*        <ContentLineText className={'whiteFont margin0 padding0'}>*/}
      {/*          previousGateBasedOnCurrent : [*/}
      {/*          {playbarState?.previousGateBasedOnCurrent && playbarState.previousGateBasedOnCurrent.sequence}]*/}
      {/*        </ContentLineText>*/}
      {/*        <ContentLineText className={'whiteFont margin0 padding0'}>*/}
      {/*          latestGateBasedOnCurrent : [*/}
      {/*          {playbarState?.latestGateBasedOnCurrent && playbarState.latestGateBasedOnCurrent.sequence}]*/}
      {/*        </ContentLineText>*/}
      {/*        <ContentLineText className={'whiteFont margin0 padding0'}>*/}
      {/*          nextGateBasedOnCurrent : [*/}
      {/*          {playbarState?.nextGateBasedOnCurrent && playbarState.nextGateBasedOnCurrent.sequence}]*/}
      {/*        </ContentLineText>*/}
      {/*      </RightContent>*/}
      {/*    </ContentRow>*/}
      {/*  </InfoDiv>*/}
      {/*</Draggable>*/}
    </div>
  );
};
export default BasicLayout;
