import React, { useCallback, useEffect, useRef, useState } from 'react';
import { APIProvider } from '@vis.gl/react-google-maps';
import useCustomGates from '@hooks/useCustomGates';
import { ColorSetting, IGate, NullableCoordinate, ToleranceRange } from '@typings/db';
import useCustomPlaybar from '@hooks/useCustomPlaybar';
import useCustomSetting from '@hooks/useCustomSetting';
import {
  calculateSpeed,
  convertEnumColorToString,
  convertEnumRefreshIntervalToNumber,
  convertEnumToleranceRangeToNumber,
} from '@utils/displayUtil';
import { toast } from 'react-toastify';
import { RefreshIcon, TestButton } from '@components/Map/styles';
import { useParams } from 'react-router';
import useCustomMember from '@hooks/useCustomMember';
import { createCoordinate, createPosition } from '@utils/mapUtil';

const MapComponent: React.FC = () => {
  const { url } = useParams<{ url: string }>();
  const { isLogin } = useCustomMember();
  const { gatesState, getGatesAsyncHook, updateGatesWithIndex } = useCustomGates();
  const { playbarState } = useCustomPlaybar();
  const { selectedPoint, isLive } = playbarState;
  const { settingState } = useCustomSetting();
  const { colorSetting, toleranceRange } = settingState;

  const [selectedCoordinate, setSelectedCoordinate] = useState<NullableCoordinate | null>(null);
  const [countdown, setCountdown] = useState<number>(5);

  const containerRef = useRef<HTMLDivElement | null>(null);
  const mapRef = useRef<google.maps.Map | null>(null);
  const zoomRef = useRef<number>(13); // 초기 zoom
  const centerRef = useRef<google.maps.LatLngLiteral>(
    gatesState[0]?.coordinate
      ? { lat: gatesState[0].coordinate.latitude, lng: gatesState[0].coordinate.longitude }
      : { lat: 37.5665, lng: 126.978 },
  );
  const markersRef = useRef<{
    allMarkers: google.maps.marker.AdvancedMarkerElement[];
    selectedMarker: google.maps.marker.AdvancedMarkerElement | null;
  }>({
    allMarkers: [],
    selectedMarker: null,
  });
  const polylinesRef = useRef<google.maps.Polyline[]>([]);

  // 중간자 지속적 update
  useEffect(() => {
    if (selectedPoint) {
      setSelectedCoordinate(selectedPoint.coordinate);
    }
  }, [selectedPoint, selectedCoordinate]);

  // 카운트 다운 후에 api 요청
  useEffect(() => {
    if (isLive) {
      if (countdown > 0) {
        const interval = setInterval(() => setCountdown(countdown - 1), 1000);
        return () => clearInterval(interval);
      } else {
        handleRefresh().then();
      }
    }
  }, [countdown, isLive, settingState.refreshInterval]);

  // 지도 초기 로드
  useEffect(() => {
    if (containerRef.current && !mapRef.current) {
      mapRef.current = new google.maps.Map(containerRef.current, {
        center: centerRef.current,
        zoom: zoomRef.current,
        mapId: '70c296db922d358d',
      });
    }
  }, []);

  // markers polyline 리랜더링
  useEffect(() => {
    updateMarkers().then();
    updatePolylines().then();
  }, [gatesState, colorSetting, toleranceRange]);

  // selectedMarker 리랜더링
  useEffect(() => {
    updateSelectedMarker();
  }, [selectedCoordinate]);

  // 모든 Marker를 초기화
  const clearAllMarkers = useCallback(() => {
    markersRef.current.allMarkers.forEach((marker) => {
      marker.map = null;
    });
    markersRef.current.allMarkers = [];
  }, []);

  // 선택된 Marker만 초기화
  const clearSelectedMarker = useCallback(() => {
    if (markersRef.current.selectedMarker) {
      markersRef.current.selectedMarker.map = null;
      markersRef.current.selectedMarker = null;
    }
  }, []);

  // polyline 초기화
  const clearPolylines = useCallback(() => {
    polylinesRef.current.forEach((polyline) => polyline.setMap(null));
    polylinesRef.current = [];
  }, []);

  // marker
  const updateMarkers = useCallback(async () => {
    clearAllMarkers();

    gatesState.forEach((gate, index) => {
      const { latitude: lat, longitude: lng } = gate.coordinate;
      const gateTag = createGateContent(gate.sequence);

      if (lat && lng) {
        const marker = new google.maps.marker.AdvancedMarkerElement({
          position: { lat: gate.coordinate.latitude, lng: gate.coordinate.longitude },
          map: mapRef.current,
          content: gateTag,
          gmpDraggable: true,
        });
        marker.addListener('dragend', (event: google.maps.MapMouseEvent) => {
          const latLng = event.latLng;
          if (latLng) {
            const newCoordinate = createCoordinate(latLng.lat(), latLng.lng());
            updateGatesWithIndex(index, newCoordinate);
            centerRef.current = {
              lat: latLng.lat() || centerRef.current.lat,
              lng: latLng.lng() || centerRef.current.lng,
            };
          }
        });

        markersRef.current.allMarkers.push(marker);
      }
    });
  }, [clearAllMarkers, gatesState, updateGatesWithIndex]);

  // polyline
  const updatePolylines = useCallback(async () => {
    clearPolylines();

    gatesState.forEach((gate, index) => {
      if (index < gatesState.length - 1) {
        const path = [
          { lat: gate.coordinate.latitude, lng: gate.coordinate.longitude },
          { lat: gatesState[index + 1].coordinate.latitude, lng: gatesState[index + 1].coordinate.longitude },
        ];

        const strokeColorEnum = determineStrokeColor(
          index,
          gatesState[index - 1],
          gatesState[index],
          gatesState[index + 1],
          colorSetting,
          toleranceRange,
        );
        const strokeColorString = strokeColorEnum === '#999999' ? '#999999' : convertEnumColorToString(strokeColorEnum);
        const polyline = new google.maps.Polyline({
          path: path,
          strokeColor: strokeColorString,
          geodesic: true,
          strokeOpacity: 1.0,
          strokeWeight: 5,
          map: mapRef.current,
        });

        polylinesRef.current.push(polyline);
      }
    });
  }, [clearPolylines, gatesState, colorSetting, toleranceRange]);

  // selectedPoint
  const updateSelectedMarker = useCallback(async () => {
    clearSelectedMarker();
    if (selectedCoordinate) {
      const { latitude: lat, longitude: lng } = selectedCoordinate;
      if (lat && lng) {
        const markerPosition: google.maps.LatLngLiteral = createPosition(lat, lng);

        const pointTag = createSelectedMarkerContent();

        markersRef.current.selectedMarker = new google.maps.marker.AdvancedMarkerElement({
          position: markerPosition,
          map: mapRef.current,
          content: pointTag,
          gmpDraggable: true,
        });
      }
    }
  }, [clearSelectedMarker, selectedCoordinate]);

  const determineStrokeColor = (
    index: number,
    previousGate: IGate,
    currentGate: IGate,
    nextGate: IGate,
    colorSetting: ColorSetting,
    toleranceRange: ToleranceRange,
  ) => {
    if (
      currentGate.time === null ||
      currentGate.traveledDistance === null ||
      nextGate.time === null ||
      nextGate.traveledDistance === null
    ) {
      return '#999999';
    }
    if (index === 0 || previousGate.time === null || previousGate.traveledDistance === null) {
      return colorSetting.initialColor;
    }
    const previousSegmentSpeed: number = calculateSpeed(previousGate, currentGate) as number;
    const currentSegmentSpeed = calculateSpeed(currentGate, nextGate) as number;
    const speedDiff = Math.abs(currentSegmentSpeed - previousSegmentSpeed);
    const tolerance = (convertEnumToleranceRangeToNumber(toleranceRange) / 100) * currentSegmentSpeed;

    if (speedDiff <= tolerance) return colorSetting.constantSpeedColor;
    return currentSegmentSpeed > previousSegmentSpeed ? colorSetting.accelerationColor : colorSetting.decelerationColor;
  };

  // Marker 콘텐츠 생성 함수
  const createGateContent = (sequence: number) => {
    const div = document.createElement('div');
    div.className = 'gate-tag';
    div.textContent = `Gate ${sequence}`;
    return div;
  };

  const createSelectedMarkerContent = () => {
    const div = document.createElement('div');
    div.className = 'point-tag';
    div.textContent = 'Selected';
    return div;
  };

  const handleRefresh = useCallback(async () => {
    if (url) {
      await getGatesAsyncHook(url);
      toast.dismiss();
      toast.success('Data refreshed');
      setCountdown(convertEnumRefreshIntervalToNumber(settingState.refreshInterval));
    }
  }, [url, getGatesAsyncHook, settingState.refreshInterval]);

  return (
    <>
      {isLive && isLogin && <RefreshIcon onClick={handleRefresh}>{countdown}</RefreshIcon>}
      {/*<APIProvider apiKey={'AIzaSyDfXQ99l7TWfyfvujf8d52Ug1EDl5ok20M'}>*/}
      <APIProvider apiKey={'AIzaSyCfTicyWufxJ5OqE32Yn4t4fJo0vI72gUc'}>
        <div ref={containerRef} style={{ width: '100%', height: '100%' }} />
      </APIProvider>
    </>
  );
};

export default MapComponent;
